<template>
  <li class="catalog__item" :product="product">
    <router-link class="catalog__pic" :to="{name: 'product', params: {id: product.id}}">
      <img :src="product.image" :alt="product.title">
    </router-link>

    <h3 class="catalog__title">
      <a href="#">
        {{ product.title }}
      </a>
    </h3>

    <span class="catalog__price">
      {{product.price | numberFormat}} ₽
    </span>

    <ul class="colors colors--black">
      <li class="colors__item" v-for="color in product.colors" :key="color.id">
        <label class="colors__label" :for="product.id + color.id">
          <input :id="product.id + color.id" class="colors__radio sr-only" type="radio" :name="product.id" :value="color.id">
          <span class="colors__value" :style="background(color.nameColor)"></span>
        </label>
      </li>
    </ul>
  </li>
</template>

<script>
import gotoPage from '@/helpers/gotoPage'
import numberFormat from '@/helpers/numberFormat';

export default {
  data() {
    return {
      color: '#73B6EA',
    };
  },
  methods: {
    gotoPage,
    background(colvalue) {
      return {
        'background-color': colvalue,
      };
    },
  },
  props: ['product'],
  filters: {
    numberFormat
  }
}
</script>
