<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            Корзина
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        3 товара
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order" >
        <div class="cart__field" v-if="formLoading"><Spinner/></div>
        <div class="cart__field" v-else>
          <div class="cart__data">
            <BaseFromText v-model="formData.name" type="text" title="ФИО" :error="formError.name" placeholder="Введите ваше полное имя" />

            <!-- <BaseFormField :title="ФИО" :error="formError.name">
            <input class="form__input" type="text" v-model="formData.name" name="name" placeholder="Введите ваше полное имя">
            </BaseFormField> -->

            <BaseFromText v-model="formData.address" type="text" title="Адрес доставки" :error="formError.address" placeholder="Введите ваш адрес" />

            <BaseFromText v-model="formData.phone" type="tel" title="Телефон" :error="formError.phone" placeholder="Введите ваш телефон" />

            <BaseFromText v-model="formData.email" type="email" title="Email" :error="formError.email" placeholder="Введите ваш Email" />

            <BaseFormTextarea title="Комментарий к заказу" v-model="formData.comment" placeholder="Ваши пожелания" :error="formError.comment" />

          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <OrderItem v-for="item in products" :key="item.productId" :item="item"/>
          </ul>
          
          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{ $store.state.cartProducts.length }}</b> товара на сумму <b>{{ totalPrice | numberFormat }} ₽</b></p>
          </div>

          <button class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFromText from '@/components/BaseFromText.vue'
import BaseFormTextarea from '@/components/BaseFormTextarea.vue'
import numberFormat from '@/helpers/numberFormat'
import { mapGetters } from 'vuex'
import OrderItem from '@/components/OrderItem.vue'
import axios from "axios";
import {API_BASE_URL} from "../config";
import Spinner from "@/components/Spinner.vue";

export default {
  data() {
    return {
      formData: {}, //св-во для хранения полей, сами поля будут создаваться автоматически
      formError: {},
      formErrorMessage: '',
      formLoading: false
    }
  },
  filters: {numberFormat},
  components: {BaseFromText, BaseFormTextarea, OrderItem, Spinner},
  computed: {
    ...mapGetters({products: 'cartDetailProducts', totalPrice: 'cartTotalPrice'})
  },
  methods: {
    order() {
      this.formError = {}; 
      this.formErrorMessage = '';
      this.formLoading = true;

      clearTimeout(this.loadFormTimer);
      this.loadFormTimer = setTimeout(() => {
        axios.post(API_BASE_URL + '/api/orders', {
        ...this.formData
      }, {
        params: {
          userAccessKey: this.$store.state.userAccessKey
        }
      })
      .then(response => {
        this.$store.commit('resetCart');
        this.$store.commit('updateOrderInfo', response.data); 
        this.$router.push({name: 'orderInfo', params: {id: response.data.id}}); 
      })
      .catch(error => { 
        this.formError = error.response.data.error.request || {};
        this.formErrorMessage = error.response.data.error.message;
      })
      .then(() => this.formLoading = false)
      .then(() => this.formData = {})
      }, 2000)
      
    }
  }
}
</script>