<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item" >
      <a class="pagination__link pagination__link--arrow" @click.prevent="btnLeft()" aria-label="Предыдущая страница" :class="{'pagination__link--disabled': page === 1}">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>
    <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber" @click.prevent="paginate(pageNumber)">
      <a class="pagination__link" :class="{'pagination__link--current': pageNumber === page}">
        {{ pageNumber }}
      </a>
    </li>
    <li class="pagination__item">
      <a class="pagination__link pagination__link--arrow" @click.prevent="btnRight()" href="#" aria-label="Следующая страница" :class="{'pagination__link--disabled': page === pages}">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
  </ul>
</template>

<script>
export default {
  model: {
    prop: 'page',
    event: 'paginate'
  },
  props: ['page', 'count', 'perPage'],
  computed: {
    pages() {
      return Math.ceil(this.count / this.perPage);
    }
  },
  methods: {
    paginate(page) {
      this.$emit('paginate', page);
    },
    btnRight() {
      if (this.page < this.pages) {
       this.$emit('paginate', this.page + 1); 
      }
    },
    btnLeft() {
      if (this.page > 1)
      this.$emit('paginate', this.page - 1);
    }
  }
}
</script>

<style>
  .btn-disabled {
    pointer-events: none;
  }
</style>